import {createRouter, createWebHistory} from 'vue-router';
import Home from '@/views/Home.vue';
import CreateCell from '@/views/CreateCell.vue';
import CreatePack from '@/views/CreatePack.vue';
import PerformCell from '@/views/perform-cell/PerformCell.vue';
import PerformCellSimulation from '@/views/perform-cell/perform-cell-simulation/PerformCellSimulation.vue';
import SetupElectricalSimulation from '@/views/perform-cell/perform-cell-simulation/setup-electrical-simulation/SetupElectricalSimulation.vue';
import SetupElectricalSimulationByImport from '@/views/perform-cell/perform-cell-simulation/setup-electrical-simulation/setup-electrical-simulation-by-import/SetupElectricalSimulationByImport.vue';
import SetupElectricalSimulationByImportForm from '@/views/perform-cell/perform-cell-simulation/setup-electrical-simulation/setup-electrical-simulation-by-import/SetupElectricalSimulationByImportForm.vue';
import SetupElectricalSimulationFromScratch from '@/views/perform-cell/perform-cell-simulation/setup-electrical-simulation/setup-electrical-simulation-from-scratch/SetupElectricalSimulationFromScratch.vue';
import OpenCellElectricalSimulation from '@/views/perform-cell/perform-cell-simulation/open-cell-electrical-simulation/OpenCellElectricalSimulation.vue';
import OpenCellElectricalSimulationForm from '@/views/perform-cell/perform-cell-simulation/open-cell-electrical-simulation/OpenCellElectricalSimulationForm.vue';
import MonitorStatusCellElectricalSimulation from '@/views/perform-cell/perform-cell-simulation/monitor-status-cell-electrical-simulation/MonitorStatusCellElectricalSimulation.vue';
import MonitorStatusCellElectricalSimulationTable from '@/views/perform-cell/perform-cell-simulation/monitor-status-cell-electrical-simulation/MonitorStatusCellElectricalSimulationTable.vue';
import ImportResultsCellElectricalSimulation from '@/views/perform-cell/perform-cell-simulation/import_results_cell_electrical_simulation/ImportResultsCellElectricalSimulation.vue';
import ImportResultsCellElectricalSimulationForm from '@/views/perform-cell/perform-cell-simulation/import_results_cell_electrical_simulation/ImportResultsCellElectricalSimulationForm.vue';
import PerformCellAnalyticalThermalAnalysis from '@/views/perform-cell/perform-cell-analytical-thermal-analysis/PerformCellAnalyticalThermalAnalysis.vue';


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/create-cell-specification',
    name: 'CreateCell',
    component: CreateCell
  },
  {
    path: '/perform-cell',
    name: 'PerformCell',
    component: PerformCell
  },
  {
    path: '/perform-cell-simulation',
    name: 'PerformCellSimulation',
    component: PerformCellSimulation
  },
  {
    path: '/setup-electrical-simulation',
    name: 'SetupElectricalSimulation',
    component: SetupElectricalSimulation
  },
  {
    path: '/setup-electrical-simulation-by-import',
    name: 'SetupElectricalSimulationByImport',
    component: SetupElectricalSimulationByImport
  },
  {
    path: '/setup-electrical-simulation-by-import-form',
    name: 'SetupElectricalSimulationByImportForm',
    component: SetupElectricalSimulationByImportForm
  },
  {
    path: '/setup-electrical-simulation-from-scratch',
    name: 'SetupElectricalSimulationFromScratch',
    component: SetupElectricalSimulationFromScratch
  },
  {
    path: '/open-cell-electrical-simulation',
    name: 'OpenCellElectricalSimulation',
    component: OpenCellElectricalSimulation
  },
  {
    path: '/open-cell-electrical-simulation-form',
    name: 'OpenCellElectricalSimulationForm',
    component: OpenCellElectricalSimulationForm
  },
  {
    path: '/monitor-status-cell-electrical-simulation',
    name: 'MonitorStatusCellElectricalSimulation',
    component: MonitorStatusCellElectricalSimulation
  },
  {
    path: '/monitor-status-cell-electrical-simulation-table',
    name: 'MonitorStatusCellElectricalSimulationTable',
    component: MonitorStatusCellElectricalSimulationTable
  },
  {
    path: '/import-results-cell-electrical-simulation',
    name: 'ImportResultsCellElectricalSimulation',
    component: ImportResultsCellElectricalSimulation
  },
  {
    path: '/import-results-cell-electrical-simulation-form',
    name: 'ImportResultsCellElectricalSimulationForm',
    component: ImportResultsCellElectricalSimulationForm
  },
  {
    path: '/perform-cell-analytical-thermal-analysis',
    name: 'PerformCellAnalyticalThermalAnalysis',
    component: PerformCellAnalyticalThermalAnalysis
  },
  {
    path: '/create-pack',
    name: 'CreatePack',
    component: CreatePack
  }
];

const router = createRouter({
  history: createWebHistory('/albatross-kbe-frontend/'),
  routes
});

export default router;